
* {
  margin: 0px;
  padding: 0;
}

#container {
  width: 100%;
  height: 100%; 
} 


#game {
  width: 100%;
  height: 100%; 
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  /* grid-template-columns: 0.5fr 1fr 0.5fr 1fr 1fr; */
  grid-template-rows: .25fr 1fr 2fr 1fr .25fr;
  /* grid-auto-rows: minmax(100px, auto);  */
  /* flex-direction: column; */
  
}

header {
  border-bottom: 2px solid black;
  margin-bottom: 50px;
  border-style: double;
}

.title {
  font-family: 'Nanum Brush Script', cursive;
  font-size: 5rem;
  font-weight: bold;
  text-align: center;
  margin: 1rem, 0, 1rem, 0; 
}

#board-container {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-column: 2 / 4;
  grid-row: 3 / 4;
}

#board {
  display: grid;
  grid-template-columns: repeat(5, 1fr); /* five equal 'fractioned' tracks */
  box-sizing: border-box;  /*within container */
  padding: 0px;
  /* border: 2px solid black; */
  border-radius: 6px;
  box-shadow: 0px, 3px, 18px, rgba(0, 0, 0, 0.5);
}



.opponent-row {
  grid-column: 2 / 4;
  grid-row: 2 / 3;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 5px;
  
}

.on-deck-row {
  grid-column: 4;
  grid-row: 3/4;
  display:flex;
  align-items: center;
  justify-content: left;
  margin: 5px;
}

.player-row{ 
  grid-column: 2 / 4;
  grid-row: 4; 
  display: flex; 
  align-items: flex-start;
  justify-content: center; 
  margin: 5px;  
 }

