/* em dependent on parent?, rem */
.square.inactive {
  border: 0.5px solid rgb(58, 58, 58);
  display: flex;
  /* background-color: rgb(172, 123, 59); */
  background: #DBE6F6;
  background: radial-gradient(circle, #DBE6F6 1%, #bdc3c7 100%);
  min-width: 40px;
  min-height: 40px;
  border-radius: 3px;
  justify-content: center;
  align-items: center;
  box-shadow: 0px, 3px, 18px, rgba(0, 0, 0, 0.5);
  /* opacity: 0; */
}

.square.active {
  border: 0.5px solid rgb(58, 58, 58);
  display: flex;
  /* background-color: rgb(172, 123, 59); */
  background: #7eeb8e;
  background: radial-gradient(circle, #DBE6F6 1%, #9fe98c 100%);
  min-width: 40px;
  min-height: 40px;
  border-radius: 3px;
  justify-content: center;
  align-items: center;
  box-shadow: 0px, 3px, 18px, rgba(0, 0, 0, 0.5);
  cursor: pointer;
  /* opacity: 0; */
}

